

*{
    margin: 0;padding: 0;box-sizing: border-box;
  }
  .App{
    background-color: rgb(92, 86, 86);
    color: white;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: larger;
  }
  .container{
    height: 400px;
    background-color: black;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .redwala{
    background-color: red;
  }
  .display{
    height: 18%;
    width: 98%;
    font-family: 'Orbitron', sans-serif;
  }
  .button{
    height: 80%;
    width: 98%;
  }
  .row{
    width: 100%;
    height: 20%;
    display: flex;
    align-items: center;
    justify-content: space-around;
  
  }
  .numbers{
  background-color: grey;
  height: 100%;
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  }
  .numbers:hover{
  background-color: rgb(73, 71, 71);
  height: 100%;
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(207, 180, 180);
  }
  
  .zero{
    width: 50%;
    background-color:red;
  }
  .zero:hover{
    width: 50%;
    /* background-color: rgb(59, 40, 231); */
  }
  .zero1{
    background-color: rgb(237, 66, 66);
  }
  .zero1:hover{
    background-color: rgb(218, 119, 119);
  }
  .Show{
     float: right;
    margin-bottom: 20px;
  }
  .zerowala{
    background-color: gray;
  }